import React from "react";
import api from "../services/api";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import { makeStyles } from "@material-ui/core/styles";
import { CardActions, Divider } from "@material-ui/core";
import Bar from "../components/Bar";
import { useParams } from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import { Button, Grid } from '@material-ui/core';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import EmailIcon from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';

const useStyles = makeStyles(theme => ({
    card: {
        margin: "-80px 10px 0 10px",
        minHeight: "400px"
    },
    media: {
        height: 300,
    },
    root: {
        width: '100%',
    },
    chip: {
        margin: theme.spacing(0.5),
    },
    section0: {
        margin: theme.spacing(1),
    },
    section1: {
        margin: theme.spacing(3, 1),
    },
    section2: {
        margin: theme.spacing(1),
    },
    section3: {
        margin: theme.spacing(3, 1, 1),
    },
}));

const initialData = {
    nome: "",
    email: "",
    telefone: "",
    whatsapp: false
};


const Contact = props => {
    let { id } = useParams();
    const classes = useStyles();

    const [doador, setDoador] = React.useState(initialData);
    const [pet, setPet] = React.useState(initialData);

    React.useEffect(() => {

        api.get(`/api/pets/${id}`)
            .then(response => {
                setPet(response.data);
                setDoador(response.data.doador);
            })
            .catch(error => { });
    }, [id]);

    const enviarWhatsapp = () => {
        const telefone = "+55" + doador.telefone.replace(/\D/g, "");

        const msg = `Ola ${doador.nome}, vi o perfil ${pet.genero === "macho" ? "do" : "da"} ${pet.nome} ${pet.tipo === "gato" ? "🐱" : "🐶"} no TinDog e gostaria de saber mais informações. 😊`;
        let target = "";
        let isMobile = (function (a) {
            if (/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(a) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw-(n|u)|c55\/|capi|ccwa|cdm-|cell|chtm|cldc|cmd-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc-s|devi|dica|dmob|do(c|p)o|ds(12|-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(-|_)|g1 u|g560|gene|gf-5|g-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd-(m|p|t)|hei-|hi(pt|ta)|hp( i|ip)|hs-c|ht(c(-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i-(20|go|ma)|i230|iac( |-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|-[a-w])|libw|lynx|m1-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|-([1-8]|c))|phil|pire|pl(ay|uc)|pn-2|po(ck|rt|se)|prox|psio|pt-g|qa-a|qc(07|12|21|32|60|-[2-7]|i-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h-|oo|p-)|sdk\/|se(c(-|0|1)|47|mc|nd|ri)|sgh-|shar|sie(-|m)|sk-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h-|v-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl-|tdg-|tel(i|m)|tim-|t-mo|to(pl|sh)|ts(70|m-|m3|m5)|tx-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas-|your|zeto|zte-/i.test(a.substr(0, 4))) {
                return true
            } else {
                return false
            }
        })(navigator.userAgent || navigator.vendor || window.opera);

        // checar
        if (isMobile) {
            target = `whatsapp://send?phone=${encodeURIComponent(telefone)}&text=${encodeURIComponent(msg)}`;

        } else {
            target = `https://api.whatsapp.com/send?phone=${encodeURIComponent(telefone)}&text=${encodeURIComponent(msg)}`;
        }

        let a = document.createElement('a');
        a.target = '_blank'
        a.href = target;
        a.click();
    };


    return (

        <React.Fragment>
            <Bar back={true} title="Contato Doador" />
            <Card className={classes.card} elevation={3} >

                <CardContent>
                    <div className={classes.section1}>
                        <Grid container alignItems="center">
                            <Grid item xs>
                                <Typography gutterBottom variant="h4">
                                    {doador.nome}
                                </Typography>
                            </Grid>
                        </Grid>
                        <Typography color="textSecondary" variant="body2">
                            <PhoneIcon /> {doador.telefone}
                        </Typography>
                        <Typography color="textSecondary" variant="body2">
                            <EmailIcon /> {doador.email}
                        </Typography>
                    </div>
                    <Divider />
                    <div className={classes.section2}>
                        <Grid container alignItems="center">
                            <Grid item xs>

                                <Button
                                    fullWidth
                                    disabled={!doador.whatsapp}
                                    onClick={enviarWhatsapp}
                                    variant="contained"
                                    color="secondary"
                                    className={classes.button}
                                    startIcon={<WhatsAppIcon />}
                                >
                                    Conversar WhatsApp
                                </Button>
                            </Grid>

                        </Grid>

                    </div>

                </CardContent>
                <CardActions className={classes.action}>

                </CardActions>
            </Card>

        </React.Fragment>
    )
};

export default Contact;